.heading{
  position: absolute;
  top: 20%;
  left: 33%;
  text-align: center;
  color: beige;
  cursor: pointer;    
  text-shadow: 5px 0px 6px gold;
}

.explore-container {
  position: absolute;
  top: 27%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 0.1rem 0.4rem;
  cursor: pointer;
  background-color: beige;
  opacity: 0.8;
}
.explore-container a{
  text-decoration: none;
  color: rgb(168, 47, 128);
}

.explore-container:hover{
  opacity: 0.7;
  transition: 0.25s ease-in-out;
}

.card-container-heading{
  padding-top: 2rem;
